body {
    font-family: Arial, Helvetica, sans-serif;
    background-color: #f0f0f0;
    margin: 0;
}

.app {
    background: linear-gradient(to bottom, #87ceeb, #98fb98);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: auto;
    height: 100vh;
}

.tops {
    margin-top: 25px;
    margin-bottom: 20px;
    text-align: center;
}

.modes {
    margin: 10px;
    padding: 15px 25px;
    font-size: 18px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s;
}

.modes:hover {
    background-color: #4CAF50;
}

.show_preds {
    margin: 20px auto;
    text-align: center;
}

.sub_modes {
    margin: 5px;
    padding: 10px 15px;
    font-size: 16px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s;
}

.sub_modes:hover {
    background-color: #4CAF50;
}

.result-container {
    margin-top: 20px;
    text-align: center;
}

.result-box {
    border: 2px solid #ccc;
    border-radius: 5px;
    padding: 10px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 60%;
    margin: auto;
}

.slider-container {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

.value-box {
    width: 60px;
    height: 60px;
    border: 1px solid #ccc;
    border-radius: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 10px;
}

.sliders {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.slider1,
.slider2,
.slider3,
.slider4,
.slider5 {
    width: 70%;
    margin: 0 5px;
}

/* Add this to center the result-container */
.result-container {
    margin-top: 20px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; /* Center content vertically */
    width: 30%;
    height: 55%;
}

.result-image {
    width:100%;
    height:45%;
    max-height: 400px;
    margin-top: 20px;
    margin-bottom: 15px;
}

.result-image img{
    width: 100%; 
    height: 100%;
}

.result-box {
    border-radius: 5px;
    padding: 10px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    width: 80%;
    margin: auto;
    height: 80%;
}

.footer {
    background-color: #4CAF50;
    color: white;
    text-align: center;
}

.footer .name {
    font-size: 18px;
}

